import React from "react";
import Navbar from '../components/navbar'
import SEO from "../components/seo";

function AboutPage() {
  return (
    <>
    <Navbar/>
      <div className="max-w-lg w-full lg:flex mx-auto pt-4 px-4 mb-24">
        <div className="shadow-lg hover:shadow-2xl bg-white rounded p-4 flex flex-col justify-between leading-normal">
          <div className="mb-8">
            <div className="text-gray-800 font-bold canela text-2xl mb-2">What is Canelita?</div>
            <p className="text-gray-700 text-base">Canelita is an online curation of Latin American artists across many art mediums. Where I've felt Latin Americans have been largely underrepresented in art celebration-- I seek to uplift and support Latin American artists through this platform.</p>
          </div>
          <div className="flex items-center">
            <div className="text-sm">
              <p className="text-gray-700 leading-none">This is made by Paulie Rodriguez, I built this with Gatsby.js and TailwindCSS, one is more than welcome to contribute to the project repo. </p>
            </div>
          </div>
        </div>
      </div>  
      </>
  );
}

export default AboutPage;
